.home {
  font-family: 'Roboto', sans-serif;
  text-align: center;
  padding: 50px 20px;
  background-color: #111;
  color: #f5f5f5;
}

.hero {
  margin-bottom: 40px;
}

.hero h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 30px;
}

.hero-image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}

.features {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.feature {
  background-color: #222;
  padding: 20px;
  border-radius: 8px;
  margin: 10px;
  flex: 1;
  max-width: 300px;
}

.feature h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.feature p {
  font-size: 1rem;
}
